<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示自营店铺信息</li>
      <li>提供对自营店铺的新增、删除、修改等操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
      <section class="cms-batch">
        <cms-button
          icon="add"
          v-prem="'/selfShops/save'"
          type="success"
          size="small"
          @click="$routerLink('/selfShops/save','save')"
        >新增</cms-button>
        <cms-button
          icon="error"
          v-prem="'/selfShops/delete'"
          type="error"
          size="small"
          @click="deleteDatas()"
        >批量删除</cms-button>
      </section>
    </cms-sticky>
    <el-table
      :data="tableData"
      style="width: 100%"
      stripe
      v-loading="loading"
      @selection-change="getCheckItems"
      @row-click="checkRows"
      ref="dataTable"
    >
      <el-table-column type="selection" width="46" fixed="left"></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="storeName" label="店铺名称" min-width="160"></el-table-column>
      <el-table-column prop="storeUsername" label="商家账号" min-width="160"></el-table-column>
      <el-table-column prop="member.username" label="店家账号" min-width="160"></el-table-column>
      <el-table-column prop="createTime" label="开店时间" min-width="140" align="center">
        <template slot-scope="scope">{{$moment(scope.row.createTime)}}</template>
      </el-table-column>
      <el-table-column label="操作" width="300" fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button
            icon="edit"
            type="info"
            size="small"
            v-prem="'/shopManage/shopCategory/list'"
            @click.stop="$routerLink('/shopManage/shopCategory/list','selfShop',{id:scope.row.id})"
          >查看店铺分类</cms-button>
          <cms-button
            icon="edit"
            v-prem="'/selfShops/edit'"
            size="small"
            type="info"
            @click.stop="$routerLink('/selfShops/edit','update',{id:scope.row.id})"
          >编辑</cms-button>
          <cms-button
            icon="error"
            v-prem="'/selfShops/delete'"
            size="small"
            type="info"
            @click.stop="deleteData(scope.row.id)"
          >删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { selfShopsList, selfShopsDelete } from '@/api/shop/selfShops.js'
var defaultData = {
  storeName: '',
  page: 1,
  size: 10
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(selfShopsDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(selfShopsDelete, this.ids) // 这个才是真实的删除方法
    }
  },
  created() {
    // 首次触发
    this.getTableData(selfShopsList, this.queryParams)
  }
}
</script>

<style>
</style>



